.pldlogin {
  min-width: 1200px;
  background: url(../../static/img/bg.f5c3016.png) no-repeat;
  background-size: 100% 100%;
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 25;
  background-color: #12d1ed;
  left: 0;
}
.pldlogin .pldlogomore {
  margin-top: 20px;
  margin-left: 30px;
  position: absolute;
  top: 5px;
  left: 30px;
}
.pldlogin .pldlogincontent {
  position: absolute;
  z-index: 9;
  width: 323px;
  background-color: #fff;
  border-radius: 6px;
  padding: 25px 21px;
  left: 56%;
  top: 25%;
}
.pldlogin .pldlogincontent .pldadddiv {
  position: relative;
  z-index: 9;
}
.pldlogin .pldlogincontent .pldloginp {
  color: #333;
  font-size: 28px;
}
.pldlogin .pldlogincontent .pldpld {
  margin-top: 13px;
  position: relative;
}
.pldlogin .pldlogincontent .pldpld input {
  height: 40px;
  width: 94%;
  padding-left: 15px;
  margin-top: 20px;
  border: 1px solid #dbdbdb;
  border-radius: 4px;
  position: relative;
}
.pldlogin .pldlogincontent .pldpld input::-webkit-input-placeholder {
  color: #dbdbdb;
}
.pldlogin .pldlogincontent .pldpld p {
  /*display: none;*/
  position: absolute;
  top: -23px;
  color: red;
}
.pldlogin .pldlogincontent .pldmorepass {
  padding-left: 2px;
  margin-top: 20px;
  margin-bottom: 20px;
  position: relative;
  overflow: hidden;
  height: 20px;
}
.pldlogin .pldlogincontent .pldmorepass a {
  position: absolute;
  top: 1px;
  color: #999;
  cursor: pointer;
  margin-left: 4px;
  left: 19px;
}
.pldlogin .pldlogincontent .pldmorepass:after {
  content: '';
  display: block;
  width: 16px;
  height: 16px;
  border: 1px solid #c3c2c2;
  position: absolute;
  top: 3px;
  left: 0px;
}
.pldlogin .pldlogincontent .pldbtn button {
  height: 40px;
  width: 100%;
  margin-top: 20px;
  outline: none;
  border: none;
  border-radius: 4px;
